* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* card--Wrapper */
.card--wrapper {
  width: 100%;
  height: 100vh;

  background-color: #000;

  display: flex;
  justify-content: center;
  align-items: center;
}

/* card */
.card {
  width: 250px;
  aspect-ratio: 1;

  padding: 15px;
  border-radius: 8px;

  color: #FFF;
  background-image: url(../src/Images/cardBg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/* card--logo */
.card--logo {
  width: 35px;
}

.card--logo img {
  width: 100%;
}

.card--txt {
  text-align: center;
}

.card--txt h1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card--txt h1 .picBtn--txt {
  display: flex;
  align-items: center;

  margin-block-end: 20px;
}

.card--txt .card--img {
  width: 80px;

  display: flex;
  justify-content: center;
  align-items: center;

  border: 3px solid #FFF;
  border-radius: 50%;
}

.card--txt .card--img img {
  width: 100%;
  border-radius: 50%;
}

/* Pop up */
.details--popUp,
.useCard--popUp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .2);

  display: flex;
  justify-content: center;
  align-items: center;
}

.details--popUp .popUp--inner,
.useCard--popUp .popUp--inner {
  position: relative;

  padding: 32px;

  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  border-radius: 8px;

  color: #F9F9F9;
  background-color: #202020;
}

.details--popUp .popUp--inner .close--btn {
  position: absolute;
  top: -30px;
  right: -10px;

  font-size: 20px;

  color: #F9F9F9;
  background-color: transparent;
}

/* Img input field */
.Img--input {
  display: none;
}

label {
  padding: 25px;

  border-radius: 8px;
  border: 1px #393939 dashed;

  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

label span {
  padding: 5px;

  border-radius: 8px;
  border: 1px #393939 solid;
}

.laterIMg {
  width: 100%;
}